@font-face {
  font-family: QuincyBlack;
  src: url("fonts/QuincyCF-Black.woff");
}

@font-face {
  font-family: QuincyMedium;
  src: url("fonts/QuincyCF-Medium.woff");
}

@font-face {
  font-family: SatoshiRegular;
  src: url("fonts/satoshi-regular-webfont.woff");
}

@font-face {
  font-family: SatoshiBold;
  src: url("fonts/satoshi-bold-webfont.woff");
}

html {
  -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
  text-align: center;
  font-family: "SatoshiRegular", system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 20px;
  color: #686261;
  line-height: 1.5rem;
  margin: 0;
  padding: 0;
}

a {
  color: #6C93AC;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.content {
  width: 100%;
  max-width: 760px;
  margin: 0 auto;
}

.intro {
  margin-bottom: 96px;

  img {
    width: 140px;
    margin-bottom: 40px;
  }

  h1 {
    font-family: "QuincyMedium";
    font-weight: normal;
    color: #C38A37;
    font-size: 32px;
    margin: 0 0 24px;
  }
  
  h2 {
    font-family: "QuincyBlack";
    font-weight: normal;
    color: #524D4B;
    font-size: 48px;
    margin: 0 0 48px;
    text-transform: uppercase;
  }
  
  @media (max-width: 760px) {
    line-height: 2rem;
  }

  p {
    line-height: 1.75rem;
  }

}

.work {
  
  div {
    margin-bottom: 80px; 
  }
  
  small {
    font-family: "SatoshiBold";
    color: #A29889;
    text-transform: uppercase;
    font-size: 12px;
    letter-spacing: 2px;
    display: block;
    margin-bottom: 20px;
  }
  
  h3 {
    font-weight: normal;
    font-size: 20px;
    color: #524D4B;
    margin: 0 0 12px;
    
  }
  
  p {
    font-size: 16px;
    max-width: 600px;
    margin: 0 auto;
  }
  
  p.links {
    color: #D9D4D0;
  }
}

footer {
  text-align: center;
  padding-top: 16px 0;
  border-top: 1px solid #D9D4D0;
  
  p {
    font-size: 12px;
    color: #686261
  }
}